/*
This is the extracted copied part responsible for showing & hiding the submenu.
*/

.nav__submenu {
  display: none;
}


.nav__menu-item:hover .nav__submenu {
  display: block;
}


/*
Layout styles.

I like to work on stuff that's good looking so I remixed a cool simple menu by Mike Rojas (thanks!): https://codepen.io/mikerojas87/pen/rojKb 
*/
:root {
  --color-blue: #00aeef;
  --color-blue-dark: #0d2035;
  --submenu-width: 180px;
}

.menu-container {
  text-align: center;
}


nav ul {
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
}


.nav {
  display: inline-block;
  margin: 2rem auto 0;
  background: --color-blue-dark;
  color: #fff;
  text-align: left;
}

.nav a {
  display: block;
  padding: 0 16px;
  line-height: inherit;
  cursor: pointer;

}

.nav__menu {
  line-height: 45px;
  font-weight: 700;
  text-transform: uppercase;
}

.nav__menu-item {
  display: inline-block;
  position: relative;
}

.nav__menu-item:hover {
  background-color: --color-blue;
}

.nav__menu-itemnav__submenu {
  display: block;
}

.nav__submenu {
  font-weight: 300;
  text-transform: none;
  display: none;
  position: absolute;
  width: --submenu-width;
  background-color: --color-blue;
}

.nav__submenu-item:hover {
  background: rgba(#000, 0.1);

}